* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.button-container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  border-radius: 50%;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 20px;
}

button[data-start], button[data-stop] {
  opacity: .8;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-right: 20px;
  font-weight: bold;
}

button[data-start] {
  background-color: green;
  border-color: green;
}

button[data-stop] {
  background-color: red;
  border-color: red;
}

